  margin-top: -25px;
  height: 50px;
  width: 100%;
  overflow: hidden; }
  .preloader i {
    display: block;
    width: 16px;
    height: 16px;
    background: black;
    border-radius: 16px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -8px 0 0 -8px;
    opacity: 0;
    transform: translate3d(150px, 0, 0);
    overflow: hidden;
    text-indent: -9999px; }
    .preloader i:nth-child(1) {
