      animation: google 1.75s ease-in-out infinite .6s; }

@keyframes google {
  0% {
    opacity: 0;
    transform: translate3d(150px, 0, 0); }
  30% {
    opacity: 1;
    transform: translate3d(0, 0, 0); }
  70% {
    opacity: 0.8;
